import faIcon from "@/components/kilvn-fa-icon/fa-icon.vue"
import loading from "@/components/Loading/index.vue"
import uniNavBar from '@/components/uni-nav-bar/uni-nav-bar.vue'
import uniIcon from '@/components/uni-icon/uni-icon.vue'
import Bmob from '@/utils/bmob.js'

let uid;
let that;
let search_text = '';
let page_size = 50;
export default {
	components: {
		loading,
		uniNavBar,
		faIcon,
		uniIcon
	},
	data() {
		return {
			showOptions: true, //是否显示筛选
			loading: true,
			productList: null,
			checked_option: 'createdAt',
		}
	},

















	onLoad() {
		that = this;
		uid = uni.getStorageSync('uid');
	},
	onShow() {
		uni.removeStorageSync("now_product")
		that.get_productList()
	},

	onUnload() {
		search_text = '';
		page_size = 50;
	},

	methods: {
		//头部的options选择
		selectd(type) {
			page_size = 50;
			that.checked_option = type;
			that.get_productList();
		},

		//加载更多
		load_more() {
			page_size += 50;
			that.get_productList();
		},

		//点击去到详情
		goDetail(value) {
			console.log(value)
			uni.setStorageSync("now_product", value);
			uni.navigateTo({
				url: "../good_det/good_det"
			})
		},

		//点击去到添加产品
		goAdd() {
			uni.navigateTo({
				url: "../good_add/good_add"
			})
		},

		//查询产品列表
		get_productList() {
			const query = Bmob.Query("Goods");
			query.equalTo("userId", "==", uid);
			query.limit(page_size);
			query.order("-" + that.checked_option); //按照时间降序
			query.equalTo("goodsName", "==", {
				"$regex": "" + search_text + ".*"
			});
			query.include("userId");
			query.include("goodsClass");
			query.find().then(res => {
				//console.log(res)
				this.productList = res;
				this.loading = false;
			});
		},

	}
}
